import React from "react";
import $ from "jquery";

$("body").on("click touchstart", "#filter-all", function () {
  $(".movie-card").removeClass("hidden");
});
$("body").on("click touchstart", "#filter-action", function () {
  $(".movie-card")
    .not("[data-genre*='Action'], [data-genre*='Sci-Fi']")
    .addClass("hidden");
  if (
    $(
      '.movie-card[data-genre*="Action"], .movie-card[data-genre*="Sci-Fi"]'
    ).hasClass("hidden")
  ) {
    $(
      ".movie-card[data-genre*='Action'], .movie-card[data-genre*='Sci-Fi']"
    ).removeClass("hidden");
  }
});

$("body").on("click touchstart", "#filter-comedy", function () {
  $(".movie-card").not("[data-genre*='Comedy']").addClass("hidden");
  if ($('.movie-card[data-genre*="Comedy"]').hasClass("hidden")) {
    $(".movie-card[data-genre*='Comedy']").removeClass("hidden");
  }
});
$("body").on("click touchstart", "#filter-drama", function () {
  $(".movie-card").not("[data-genre*='Drama']").addClass("hidden");
  if ($('.movie-card[data-genre*="Drama"]').hasClass("hidden")) {
    $(".movie-card[data-genre*='Drama']").removeClass("hidden");
  }
});
$("body").on("click touchstart", "#filter-kids", function () {
  $(".movie-card").not("[data-genre*='Family']").addClass("hidden");
  if ($('.movie-card[data-genre*="Family"]').hasClass("hidden")) {
    $(".movie-card[data-genre*='Family']").removeClass("hidden");
  }
});
$("body").on("click touchstart", "#filter-hard-copy", function () {
  $(".movie-card")
    .not("[data-streaming*='DVD'], [data-streaming*='Blu-Ray']")
    .addClass("hidden");
  if (
    $(
      '.movie-card[data-streaming*="DVD"], .movie-card[data-streaming*="Blu-Ray"]'
    ).hasClass("hidden")
  ) {
    $(
      ".movie-card[data-streaming*='DVD'], .movie-card[data-streaming*='Blu-Ray']"
    ).removeClass("hidden");
  }
});
$("body").on("click touchstart", "#filter-van-ready", function () {
  $(".movie-card").not("[data-van-ready*='true']").addClass("hidden");
  if ($('.movie-card[data-van-ready*="true"]').hasClass("hidden")) {
    $(".movie-card[data-van-ready*='true']").removeClass("hidden");
  }
});

const Filters = () => {
  return (
    <div className="row d-flex align-items-center movie-list__filters">
      <a className="btn col-sm-1 filter-btn" id="filter-all">
        All
      </a>
      <a className="btn col-sm-1 filter-btn" id="filter-action">
        Action
      </a>
      <a className="btn col-sm-1 filter-btn" id="filter-comedy">
        Comedy
      </a>
      <a className="btn col-sm-1 filter-btn" id="filter-drama">
        Drama
      </a>
      <a className="btn col-sm-1 filter-btn" id="filter-kids">
        Kids/Family
      </a>
      <a className="btn col-sm-1 filter-btn" id="filter-hard-copy">
        Hard Copy
      </a>
      <a className="btn col-sm-1 filter-btn" id="filter-van-ready">
        Van Ready
      </a>
    </div>
  );
};

export default Filters;
